import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Chip,
  Button,
  Grid,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Tabs,
  Tab,
  Divider,
  IconButton,
  Link
} from '@mui/material';
import {
  PlayArrow as PlayArrowIcon,
  Schedule as ScheduleIcon,
  Error as ErrorIcon,
  CheckCircle as CheckCircleIcon,
  Launch as LaunchIcon,
  Refresh as RefreshIcon
} from '@mui/icons-material';
import axios from 'axios';

const API_BASE_URL = process.env.NODE_ENV === 'production' 
  ? 'https://youtube.platformmakers.org/api' 
  : 'http://localhost:5002/api';

const History = ({ isLoggedIn, user }) => {
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedItem, setSelectedItem] = useState(null);
  const [detailOpen, setDetailOpen] = useState(false);
  const [selectedTab, setSelectedTab] = useState(0);

  useEffect(() => {
    if (isLoggedIn) {
      fetchHistory();
    }
  }, [isLoggedIn]);

  const fetchHistory = async () => {
    try {
      setLoading(true);
      const token = localStorage.getItem('token');
      
      const response = await axios.get(`${API_BASE_URL}/history`, {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (response.data.success) {
        setHistory(response.data.data.jobs);
      }
    } catch (error) {
      setError(error.response?.data?.error || '  .');
    } finally {
      setLoading(false);
    }
  };

  const getStatusInfo = (status) => {
    switch (status) {
      case 'completed':
        return { label: '', color: 'success', icon: <CheckCircleIcon /> };
      case 'processing':
        return { label: '', color: 'info', icon: <PlayArrowIcon /> };
      case 'pending':
        return { label: '', color: 'warning', icon: <ScheduleIcon /> };
      case 'failed':
        return { label: '', color: 'error', icon: <ErrorIcon /> };
      default:
        return { label: status, color: 'default', icon: <ScheduleIcon /> };
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleString('ko-KR');
  };

  const handleViewDetail = async (item) => {
    if (item.status !== 'completed') {
      setSelectedItem(item);
      setDetailOpen(true);
      return;
    }

    try {
      const token = localStorage.getItem('token');
      
      const response = await axios.get(`${API_BASE_URL}/process/result/${item.jobId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (response.data.success) {
        setSelectedItem({
          ...item,
          summaries: response.data.data.summaries
        });
        setSelectedTab(0);
        setDetailOpen(true);
      }
    } catch (error) {
      setError('  .');
    }
  };

  const getYouTubeVideoId = (url) => {
    const match = url.match(/(?:youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/|youtube\.com\/v\/)([^&\n?#]+)/);
    return match ? match[1] : null;
  };

  if (!isLoggedIn) {
    return (
      <Box sx={{ textAlign: 'center', py: 8 }}>
        <Typography variant="h5" gutterBottom>
             
        </Typography>
        <Typography variant="body1" color="text.secondary">
           .
        </Typography>
      </Box>
    );
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', py: 8 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ py: 4 }}>
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
        <Button variant="outlined" onClick={fetchHistory} startIcon={<RefreshIcon />}>
           
        </Button>
      </Box>
    );
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" gutterBottom>
           
        </Typography>
        <IconButton onClick={fetchHistory}>
          <RefreshIcon />
        </IconButton>
      </Box>

      {history.length === 0 ? (
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h6" gutterBottom>
              
          </Typography>
          <Typography variant="body1" color="text.secondary">
             YouTube  .
          </Typography>
        </Box>
      ) : (
        <Grid container spacing={3}>
          {history.map((item, index) => {
            const statusInfo = getStatusInfo(item.status);
            const videoId = getYouTubeVideoId(item.sourceUrl);
            
            return (
              <Grid item xs={12} md={6} lg={4} key={item.jobId || index}>
                <Card>
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                      <Chip
                        label={statusInfo.label}
                        color={statusInfo.color}
                        size="small"
                        icon={statusInfo.icon}
                      />
                      <Typography variant="caption" color="text.secondary">
                        {formatDate(item.createdAt)}
                      </Typography>
                    </Box>

                    {videoId && (
                      <Box sx={{ mb: 2, textAlign: 'center' }}>
                        <img
                          src={`https://img.youtube.com/vi/${videoId}/mqdefault.jpg`}
                          alt="YouTube Thumbnail"
                          style={{ 
                            width: '100%', 
                            height: '120px', 
                            objectFit: 'cover',
                            borderRadius: '8px'
                          }}
                        />
                      </Box>
                    )}

                    <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                      <Link 
                        href={item.sourceUrl} 
                        target="_blank" 
                        rel="noopener"
                        sx={{ 
                          display: 'flex', 
                          alignItems: 'center', 
                          gap: 1,
                          textDecoration: 'none'
                        }}
                      >
                        YouTube  
                        <LaunchIcon fontSize="small" />
                      </Link>
                    </Typography>

                    {item.status === 'completed' && (
                      <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                         : {item.summaryCount || 6}
                      </Typography>
                    )}

                    {item.status === 'failed' && item.errorMessage && (
                      <Typography variant="body2" color="error" sx={{ mb: 2 }}>
                        : {item.errorMessage}
                      </Typography>
                    )}

                    <Button
                      fullWidth
                      variant="outlined"
                      onClick={() => handleViewDetail(item)}
                      disabled={item.status === 'pending'}
                    >
                      {item.status === 'completed' ? ' ' : ' '}
                    </Button>
                  </CardContent>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      )}

      {/*   */}
      <Dialog
        open={detailOpen}
        onClose={() => setDetailOpen(false)}
        maxWidth="md"
        fullWidth
      >
        {selectedItem && (
          <>
            <DialogTitle>
               
              <Typography variant="body2" color="text.secondary">
                {formatDate(selectedItem.createdAt)}
              </Typography>
            </DialogTitle>
            <DialogContent>
              {selectedItem.summaries ? (
                <>
                  <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
                    <Tabs 
                      value={selectedTab} 
                      onChange={(e, newValue) => setSelectedTab(newValue)}
                      variant="scrollable"
                      scrollButtons="auto"
                    >
                      {selectedItem.summaries.map((summary, index) => (
                        <Tab
                          key={index}
                          label={summary.systemPrompt?.name || ` ${index + 1}`}
                        />
                      ))}
                    </Tabs>
                  </Box>

                  {selectedItem.summaries.map((summary, index) => (
                    <Box
                      key={index}
                      hidden={selectedTab !== index}
                    >
                      <Typography
                        variant="body1"
                        component="pre"
                        sx={{
                          whiteSpace: 'pre-wrap',
                          lineHeight: 1.8,
                          fontFamily: 'inherit',
                          maxHeight: 400,
                          overflow: 'auto'
                        }}
                      >
                        {summary.content}
                      </Typography>
                      
                      {summary.tokensUsed && (
                        <Box sx={{ mt: 2, pt: 2, borderTop: 1, borderColor: 'divider' }}>
                          <Typography variant="caption" color="text.secondary">
                             : {summary.tokensUsed.toLocaleString()} | 
                             : {summary.processingTime} |
                            AI : {summary.aiProvider}
                          </Typography>
                        </Box>
                      )}
                    </Box>
                  ))}
                </>
              ) : (
                <Box sx={{ textAlign: 'center', py: 4 }}>
                  <Chip
                    label={getStatusInfo(selectedItem.status).label}
                    color={getStatusInfo(selectedItem.status).color}
                    icon={getStatusInfo(selectedItem.status).icon}
                    sx={{ mb: 2 }}
                  />
                  <Typography variant="body1">
                    {selectedItem.status === 'processing' && ' ...'}
                    {selectedItem.status === 'failed' && `   : ${selectedItem.errorMessage}`}
                    {selectedItem.status === 'pending' && '  ...'}
                  </Typography>
                </Box>
              )}
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setDetailOpen(false)}>
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>
    </Box>
  );
};

export default History;